import React from 'react';
import Notification from 'rc-notification';
import classNames from 'classnames';
import CheckCircleO from '@wind/icons/lib/icons/CheckCircleO';
import InfoCircleO from '@wind/icons/lib/icons/InfoCircleO';
import ExclamationO from '@wind/icons/lib/icons/ExclamationO';
import CloseCircleO from '@wind/icons/lib/icons/CloseCircleO';
import CloseO from '@wind/icons/lib/icons/CloseO';
const notificationInstance = {};
let defaultDuration = 4.5;
let defaultTop = 24;
let defaultBottom = 24;
let defaultPlacement = 'topRight';
let defaultGetContainer;
function setNotificationConfig(options) {
    const { duration, placement, bottom, top, getContainer } = options;
    let needRemove = false;
    if (duration !== undefined) {
        defaultDuration = duration;
    }
    if (placement !== undefined) {
        defaultPlacement = placement;
    }
    if (bottom !== undefined) {
        defaultBottom = bottom;
        needRemove = true;
    }
    if (top !== undefined) {
        defaultTop = top;
        needRemove = true;
    }
    if (getContainer !== undefined) {
        defaultGetContainer = getContainer;
        needRemove = true;
    }
    if (needRemove) {
        removeInstance();
    }
}
// 部分配置变化时需要重新生成 Instance，故不建议经常调用 config 方法。
function removeInstance() {
    // 重新生成 Instance
    Object.keys(notificationInstance)
        .forEach(cacheKey => notificationInstance[cacheKey] = null);
}
function getPlacementStyle(placement, top = defaultTop, bottom = defaultBottom) {
    let style;
    switch (placement) {
        case 'topLeft':
            style = {
                left: 0,
                top,
                bottom: 'auto',
            };
            break;
        case 'topRight':
            style = {
                right: 0,
                top,
                bottom: 'auto',
            };
            break;
        case 'bottomLeft':
            style = {
                left: 0,
                top: 'auto',
                bottom,
            };
            break;
        default:
            style = {
                right: 0,
                top: 'auto',
                bottom,
            };
            break;
    }
    return style;
}
function getNotificationInstance({ prefixCls, placement = defaultPlacement, getContainer = defaultGetContainer, top, bottom, }, callback) {
    const cacheKey = `${prefixCls}-${placement}`;
    if (notificationInstance[cacheKey]) {
        callback(notificationInstance[cacheKey]);
        return;
    }
    Notification.newInstance({
        prefixCls,
        className: `${prefixCls}-${placement}`,
        style: getPlacementStyle(placement, top, bottom),
        getContainer,
        closeIcon: <CloseO />
    }, (notification) => {
        notificationInstance[cacheKey] = notification;
        callback(notification);
    });
}
const typeToIcon = {
    success: <CheckCircleO className='w-notification-notice-icon w-notification-notice-icon-success'/>,
    info: <InfoCircleO className='w-notification-notice-icon w-notification-notice-icon-info'/>,
    error: <CloseCircleO className='w-notification-notice-icon w-notification-notice-icon-error'/>,
    warning: <ExclamationO className='w-notification-notice-icon w-notification-notice-icon-warning'/>,
};
function notice(args) {
    const outerPrefixCls = args.prefixCls || 'w-notification';
    const prefixCls = `${outerPrefixCls}-notice`;
    const duration = args.duration === undefined ? defaultDuration : args.duration;
    let iconNode = null;
    if (args.icon) {
        iconNode = (<span className={`${prefixCls}-icon`}>
        {args.icon}
      </span>);
    }
    else if (args.type) {
        const iconType = typeToIcon[args.type];
        iconNode = (iconType);
    }
    const autoMarginTag = (!args.description && iconNode)
        ? <span className={`${prefixCls}-message-single-line-auto-margin`}/>
        : null;
    const { placement } = args;
    getNotificationInstance({
        prefixCls: outerPrefixCls,
        placement,
    }, (notification) => {
        notification.notice({
            content: (<div className={classNames(iconNode ? `${prefixCls}-with-icon` : '', !args.description ? `${prefixCls}-title-only` : '')}>
            {iconNode}
            <div className={`${prefixCls}-message`}>
              {autoMarginTag}
              {args.message}
            </div>
            <div className={`${prefixCls}-description`}>{args.description}</div>
            {args.btn ? <span className={`${prefixCls}-btn`}>{args.btn}</span> : null}
          </div>),
            duration,
            closable: true,
            onClose: args.onClose,
            onClick: args.onClick,
            key: args.key,
            style: args.style || {},
            className: args.className,
        });
    });
}
const api = {
    open: notice,
    close(key) {
        Object.keys(notificationInstance)
            .forEach(cacheKey => notificationInstance[cacheKey].removeNotice(key));
    },
    config: setNotificationConfig,
    destroy() {
        Object.keys(notificationInstance).forEach(cacheKey => {
            notificationInstance[cacheKey].destroy();
            delete notificationInstance[cacheKey];
        });
    },
};
['success', 'info', 'warning', 'error'].forEach((type) => {
    api[type] = (args) => api.open(Object.assign(Object.assign({}, args), { type }));
});
api.warn = api.warning;
export default api;
